import React, { useState, useEffect, useRef } from 'react';
import './static/scss/base.scss';
// import './static/css/ag-grid.css'
// import './static/css/ag-theme-alpine.css'
import { Grid } from 'ag-grid-community';
import 'ag-grid-community/styles//ag-grid.css';
import 'ag-grid-community/styles//ag-theme-alpine.css';

import Sidenav from './components/Navigation/Sidenav';
import Topnav from './components/Navigation/Topnav';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

function App() {

  useEffect(() => {
    const inputPicTableOptions = {
      columnDefs: [],
    };
    
    var inputPicTableDiv = document.querySelector('#inputPicTable');
    new Grid(inputPicTableDiv, inputPicTableOptions);

    fetch('../static_old/json/table.json')
      .then(function (response) {
        return response.json();
      }).then(function (data) {
        const colDefs = inputPicTableOptions.api.getColumnDefs();
        colDefs.length=0;
        const keys = Object.keys(data[0]);
        keys.forEach(key => colDefs.push({
          field: key, 
          filter: 'agMultiColumnFilter',
          filterParams: {
            filters: [
              { filter: 'agTextColumnFilter', display: 'subMenu' },
              { filter: 'agSetColumnFilter' },
            ],
          },
        }));
        inputPicTableOptions.api.setColumnDefs(colDefs);
        inputPicTableOptions.api.setRowData(data);
      })
  }, []);
  

  return (
    <div className="wrapper">
        <div className="container">
            <div className="content">
                <Sidenav/>
                <div className="main">
                    <Topnav/>
                    <div className="main-body">
                    <div className="ag-theme-alpine" style={{ height: 400, width: 600 }}>
                      <div id="inputPicTable"></div>
                    </div>
                    </div>
                </div>
            </div>    
        </div>
    </div>
  );
}

export default App;
